body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: aqua;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.progress-container {
  height: 10px;
}

.progress {
  background-color: #A9AAA9;
  height: inherit;
}
.container {
  display: block;
  flex-basis: 200px;
  flex-grow: 1;
  padding: 10px 0;
}

.content,
.answers {
  padding: 20px 0;
}

.result-container {
  text-align: center;
}

.message {
  color: #000000;
  font-size: 30px;
  margin: 16px;
}

.correct-answer {
  color: #000;
}
.base-button {
    border: 1px solid #000000;
    background-color: #E5E6E5;
    cursor: pointer;
    border-radius: 6px;
    outline: none;
    font-size: 14px;
    padding: 10px 28px;
    color: black;
  }
  
  .answer-button {
    font-size: 14px;
    font-weight: 500;
    padding: 4px;
    border-radius: 6px;
    min-width: 150px;
    margin: 12px 0;
    width: 40%;
    white-space: pre;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
    .answer-button:hover {
      background-color: black;
      color: white;
    }
  
    .answer-button:nth-child(2n) {
      float: right;
    }
  
  
  .is-answer {
    background-color: white;
    color: black;
  }
  
  .next-button {
    font-size: 18px;
    font-weight: 500;
  }
.stats-container {
  height: 32px;
  display: flex;
  justify-content: space-between;
}

.stats {
  color: #494949;
  font-size: 18px;
  padding: 8px;
}

.score-bar {
  display: flex;
  width: 100%;
  height: 28px;
  border: 2px solid #000;
  border-radius: 4px;
  background-color: white;
  align-items: center;
  position: relative;
}

.correct-score-bar {
  height: 100%;
  background-color: #000;
}

.completed-score-bar {
  height: 100%;
  background-color: #717171;
}

.expected-score-bar {
  height: 100%;
  background-color: #D2D2D2;
}
.rating {
  font-size: 12px;
  margin: 2px 2px;
  color: #eeeeee;
}

.is-valid {
  color: black;
}
.header-title {
  color: #494949;
  margin: 0;
}

.header-subtitle {
  color: #a2a2a2;
  font-weight: 400;
  font-size: 18px;
  padding: 4px 2px;
  margin: 0;
}
.app-container {
  border: 5px solid #EDEDED;
  display: flex;
  flex-direction: column;
  height: 650px;
  margin: 50px auto;
  min-width: 600px;
  width: 50%;
  background-color: white;
}

.quiz-content {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  padding: 40px 60px;
  text-align: left;
}

.quiz-score {
  color: #2466b7;
  flex-grow: 1;
  font-size: 24px;
  font-weight: 500;
  padding: 40px;
  text-align: center;
}
